import React from "react"
import Layout from "../components/layout"
import styled from "styled-components"
import chuck from "../components/chuck.png"
import { Link } from "gatsby"

const MainIndex = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #6d30bc;
  display: grid;
`

const Buttons = styled.div`
  width: 20vw;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-left: 5vw;
  margin-top: 5vh;
`

const Container = styled.div`
  width: 50vw;
  height: 100vh;
`

const H1 = styled.h1`
  @import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
  color: white;
  margin: 0;
  font-size: 4.2vw;
  padding-left: 5vw;
  padding-top: 20vh;
  font-family: "Roboto", sans-serif;
`

const H2 = styled.h2`
  @import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
  color: white;
  margin: 0;
  padding-left: 5vw;
  font-size: 3.2vw;
  padding-top: 5vh;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
`

const Button = styled.button`
  @import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
  width: 9vw;
  height: 6vh;
  color: ${({ color }) => color};
  background-color: ${({ bgcolor }) => bgcolor};
  border: ${props => (props.border ? "solid 0.2vw #4edc60" : "none")};
  font-size: 1.4vw;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  cursor: pointer;
`

const Image = styled.img`
  position: fixed;
  width: 25vw;
  height: 30vw;
  display: block;
  margin-left: 60vw;
  margin-top: 13vh;
`

export default function Home() {
  return (
    <MainIndex>
      <Layout>
        <Image src={chuck} />
        <Container>
          <H1>Chuck Norris Jokes App</H1>
          <H2>Have fun to use it</H2>
          <Buttons>
            <Link to="/app">
              <Button bgcolor="#4edc60" color="#6d30bc">
                Start
              </Button>
            </Link>
            <Link to="https://api.chucknorris.io">
              <Button bgcolor="#6d30bc" border color="#4edc60">
                Check Api
              </Button>
            </Link>
          </Buttons>
        </Container>
      </Layout>
    </MainIndex>
  )
}
